import React, { useState } from 'react'

import ChatBot from 'react-simple-chatbot'
import { ThemeProvider } from 'styled-components'
import Post from './Post'
import Link from './Link'
import '../../App.css'
import { a } from './recode'
import {getDatabase ,ref,set,onValue, push } from 'firebase/database'
import fi from './firebaseCon';

let db = getDatabase(fi);
// import Com from './Com'
const theme = {
  background: '#f5f8fb',
  fontFamily: 'Helvetica Neue',
  headerBgColor: '#0f4d4a',
  headerFontColor: '#fff',
  headerFontSize: '15px',
  botBubbleColor: '#0f4d4a',
  botFontColor: '#fff',
  userBubbleColor: '#fff',
  userFontColor: '#4a4a4a',
}

// all available config props
const config = {
  width: '350px',
  height: '450px',
  hideUserAvatar: true,
  placeholder: 'Type your response.',
  headerTitle: 'ChatBot',
}
let arr=[ {
  id: 'welcome',
 message: 'Hello!  I am jarvis',
 trigger: 'q-firstname',
},
//   /* Paste */
{
 id: 'q-firstname',
 message: 'What is your  name?',
 trigger: 'rmcbot',
},];
const UPDATEjSON =() => {
  while(true){
  // let techStack = localStorage.getItem('step');
  // console.log(techStack);
  }
  // arr.push(a[index]);
  console.log("ok");
  return;
 
}
const UPDATENameStep= (name) => {
  
  arr.push(
    {
      id: 'rmcbot',
      message:
        `Hi,${name}I am jarvis ! What can I do for you?`,
      trigger: 'qtype',
    },
    {
    id: 'qtype',
  
    options: [
          { value: 1, label: 'Property Tax ?', trigger: '4'
          },
          { value: 2, label: ' Professional Tax ?', trigger: '3'
          },
          { value: 3, label: 'Election Department', trigger: '5'
          },
          { value: 4, label: 'More Information', trigger: '6'
          },
      ],
  },);
}

const Chatbot = (props) => {
  let [showChat, setShowChat] = useState(false)

  const startChat = () => {
    setShowChat(true)
  }
  const hideChat = () => {
    setShowChat(false)
  }
  const [currentTheme, setCurrentTheme] = React.useState('');

  React.useEffect(() => {
    const starCountRef = ref(db, 'users/test');
    onValue(starCountRef, (snapshot) => {
      const data = snapshot.val();
      if(data != null){
        localStorage.setItem('step',data.step);
        console.log('step', data.step);
      }
      // console.log(data);
      // updateStarCount(postElement, data);
    });

    
  }, []);


  const callMyfunc = (id) =>{
    setTimeout(()=>{
      console.log('callMyfunc '+id,localStorage.getItem('step'));
       set(ref(db, 'users/' + "test"), {
        step : localStorage.getItem('step')
       });
    },15000)
    

      // const starCountRef = ref(db, 'users/' + "test" );
      // onValue(starCountRef, (snapshot) => {
      // const data = snapshot.val();
      // console.log(data);
     
}
      

  return (
    <ThemeProvider theme={theme}>
      <div className="bot">
      <div style={{ display: showChat ? 'none' : '' }}>
        <ChatBot 
          speechSynthesis={{ enable: true, lang: 'en-US' }}
          recognitionEnable={true}
          cache= {true}
          cacheName= 'step'
          steps= {[
                {
                  id: 'welcome',
                 message: 'Hello!  I am jarvis',
                 trigger: ()=>{("welcome"); return "q-firstname"},
                },
          //   /* Paste */
                 {
                  id: 'q-firstname',
                  message: 'What is your  name?',
                 trigger: 'firstname',
               },
                {
                 id: 'firstname',
                 user: true,
                validator: (value) => {
                if (/^[A-Za-z]+$/.test(value)) {
                  
                  return true
                } else {
                  return 'Please input alphabet characters only.'
                }
              },
              trigger: ()=>{UPDATENameStep(); return 'rmcbot'},
            },
            {
              id: 'rmcbot',
              message:
                'Hi,{previousValue} I am jarvis ! What can I do for you?',
                trigger: ()=>{UPDATENameStep("{previousValue}"); return 'qtype'},
            },
            {
              id: 'qtype',
            
              options: [
                { value: 1, label: 'Property Tax ?', trigger: ()=>{callMyfunc("hi"); return "4"}, },
                { value: 2, label: ' Professional Tax ?', trigger: ()=>{callMyfunc("hi"); return "3"}},
                { value: 3, label: 'Election Department',  trigger: ()=>{callMyfunc("hi"); return "5"}},
                { value: 4, label: 'More Information',  trigger: ()=>{callMyfunc("hi"); return "6"}},
              ],
              
            },
            {
              id: '3',
              message:
                `Profession tax is the tax levied and collected by the state governments in India.`,
                trigger: ()=>{callMyfunc("3"); return "qtype"},
            },
            {
              id: '4',
              message:
                'A property tax or millage rate is an ad valorem tax on the value of a property.',
                trigger: ()=>{callMyfunc("4"); return "qtype"},
            },
            {
              id: '5',
              message:
                'An election is a way people can choose their candidate or their preferences in a representative democracy or other form of government',
                trigger: ()=>{callMyfunc("5"); return "qtype"},
            },
            {
              id: '6',
              component: <Link />,
              trigger: ()=>{callMyfunc("6"); return "qtype"},
            },
            {
              id: 'q-submit',
              message: 'Do you have any other questions !?',
              trigger: 'submit',
            },
            {
              id: 'submit',
              options: [
                { value: 'y', label: 'Yes', trigger: 'no-submit' },
                { value: 'n', label: 'No', trigger: 'end-message' },
              ],

            },
            {
              id: 'no-submit',
              trigger: ()=>{callMyfunc('no-submit'); return "qtype"},
              options: [
                { value: 1, label: 'Property Tax ?', trigger: '4' },
                { value: 2, label: ' Professional Tax ?', trigger: '3' },
                { value: 3, label: 'Election Department', trigger: '5' },
                { value: 4, label: 'More Information', trigger: '6' },
              ],
            },
            {
              id: 'end-message',
              trigger: ()=>{callMyfunc('end-message'); return "qtype"},
              component: <Post />,
              asMessage: true,
              end: true,
            },
           ]}
          {...config}
        />
      </div>
      <div>
        {!showChat ? (
          <button className="btn" onClick={() => startChat()}>
            <i className="fa fa-minus"></i>
          </button>
        ) : (
          <button className="btn" onClick={() => hideChat()}>
            <i className="fa fa-plus"></i>
          </button>
        )}
      </div>
      </div>
    </ThemeProvider>
  )
}

export default Chatbot
